@import url('https://fonts.googleapis.com/css2?family=Changa:wght@200&family=Crimson+Text:ital,wght@1,600&family=Oswald:wght@200;300;400;500;600;700&display=swap');
 
.topcontent{
     color: white;
     position: relative;
     left: 700px;
}
.topcontent__container{
    position: absolute;
    top: 300px;
    right: 150px;
    left: 150px;
}
.topcontent__container>h1{
     
 font-size:30px;
}
.topcontent__container > p{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 3px;
    padding-top: 20px;
}
.topcontent__container >a >button{
    margin-top: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;
}
.topcontent__container > h1{
    font-size: 50px;
    font-family: "Changa" sans-serif;
}
.topcontent__downloadbutton{
    color: white;
    background-color: orangered;
    font-size: 14px;
    padding:  10px 30px;
    font-family: "Changa" sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
}
.topcontent__workbutton{
    color: white;
    border: 1px solid white !important;
    background-color: transparent;
    font-size: 14px;
    padding:9px 45px ;
    font-family:"Changa" sans-serif ;
    font-weight: 300;
    cursor: pointer;
}
.topcontent__downloadbutton:hover{
    color: orangered;
    background-color: white;
    font-weight: 600;
}
.topcontent__workbutton:hover{
  color: black;
  background-color: white;
  font-weight: 600;  
}