.contact{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0 100px;
    align-items: center;
}
.contact__container{
    border: 1px solid white;
    width: 600px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
}
.contact__container>p{
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;

}
.contact__container>p >span{
    color: orangered;

}
.contact__icons >a >button>.MuiSvgIcon-root {
    color: white;
    font-size: 30px;
}
.contact__icons >a >.MuiButtonBase-root{
    color: white !important;

}
.content{
    display: flex;
}